}
body {
    margin: 0;
    font-family: Arial, sans-serif;
    font-size: 16px;
}
.ServicesAndLicenses {
  width: 200px;
  margin: 0 auto;
}
.ServicesAndLicenses__title {
  margin-left: 0px;
  font-size: 20px;
  margin-bottom: 20px;
}
.switcher__title {
    margin: 0 0 20px;
}
.ServicesAndLicenses__switcher__input {
    display: none;
}
.ServicesAndLicenses__switcher__input:checked + .ServicesAndLicenses__switcher__label:before {
    background-color: #D03832;
}
.ServicesAndLicenses__switcher__input:checked + .ServicesAndLicenses__switcher__label:after {
    -webkit-transform: translateX(23px);
            transform: translateX(23px);
}
.ServicesAndLicenses__switcher__input:checked + .ServicesAndLicenses__switcher__label:active:after {
    -webkit-transform: translateX(12px);
            transform: translateX(12px);
}
.ServicesAndLicenses__switcher__input:checked + .ServicesAndLicenses__switcher__label:active:before {
    background-color: #D03832;
}
.ServicesAndLicenses__switcher__label {
    display: inline-block;
    vertical-align: top;
    padding-left: 50px;

    font-size: 12px;
    cursor: pointer;

    position: relative;

    -webkit-transition: color .1s linear;

    transition: color .1s linear;
    border: none;
    margin-top: 20px;
}
.ServicesAndLicenses__switcher__label:before {
    content: "";
    display: block;
    width: 65px;
    height: 40px;

    background-color: #e5e5e5;
    border-radius: 20px;

    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;

    -webkit-transition: background-color .1s linear;

    transition: background-color .1s linear;
}
.ServicesAndLicenses__switcher__label:after {
    content: "";
    display: block;
    width: 32px;
    height: 32px;

    background-color: #fff;
    border-radius: 50%;

    position: absolute;
    top: 4px;
    left: 5px;
    z-index: 2;

    -webkit-transition: -webkit-transform .1s linear;

    transition: -webkit-transform .1s linear;

    transition: transform .1s linear;

    transition: transform .1s linear, -webkit-transform .1s linear;
}
.ServicesAndLicenses__switcher__label:active:after {
    -webkit-transform: translateX(20px);
            transform: translateX(20px);
}
.ServicesAndLicenses__switcher__label:active:before {
    background-color: #D03832;
}
.switcher__text {
  margin-left: 30px; 
    line-height: 40px;
}
.tariff {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    margin-top: 30px;
    margin-bottom: 30px;
}
.tariff__in {
    margin: 0 auto;
    width: 600px;
    height: 170px;
    -webkit-box-shadow: 0px  10px 20px rgba(0,0,0,0.1);
            box-shadow: 0px  10px 20px rgba(0,0,0,0.1);  /* Параметры тени */
}
.tariff__in__tariff__title {
    font-size: 19px;
    margin: 40px 20px;
    margin-bottom: 10px;
}
.tariff__in__price {
    font-size: 34px;
    margin: 0px 20px;
}
.tariff__in__in_date {
    margin: 10px 20px;
    color: #B1B1B1;
}
.strap {
      height: 3px;
      width: 0px;
      background:#D03832 ;
      position: relative;
      left: 25px;
      top: -38px;
      pointer-events: none;
}
.row, .row input {
      width:100px;
      text-align: center;
      margin: 0 auto;
}
.row {
  width:150px
}
.row__text {
      position: relative;
      top: -20px;
      pointer-events: none;
      background: #D03832;
      width: 40px;
      line-height: 31px;
      height: 31px;
      border-radius: 20px;
      color: #FFF;
      z-index: 9999999999999999999;
}
input[type=range]::-webkit-slider-thumb {
      -webkit-appearance: none;
}
input[type=range]::-webkit-slider-runnable-track {
      height: 3px;
  cursor: pointer;
  background: #E3E3E3;
}
input[type=range]:focus::-webkit-slider-runnable-track {
  background: #E3E3E3;
}
input[type=range]::-moz-range-track {
  cursor: pointer;
  background: #E3E3E3;
}
input[type=range]::-ms-fill-lower {
  background: #E3E3E3;
}
input[type=range]:focus::-ms-fill-lower {
  background: #E3E3E3;
}
input[type=range]::-ms-fill-upper {
  background: #E3E3E3;
}
input[type=range]:focus::-ms-fill-upper {
  background: #E3E3E3;
}


